/* eslint-disable */
import Long from "long";
import * as _m0 from "protobufjs/minimal";

export const protobufPackage = "metaoslabs.metaos.poolincentives";

export interface LiquidityRewardSubConfig {
  /** the pool address */
  address: string;
  /** the total reward of weight in hundredths of a bip, i.e. 1e-6 */
  weight: number;
  /** the index of reward configs */
  index: number;
}

export interface LiquidityRewardConfig {
  id: Long;
  enabled: boolean;
  mintable: boolean;
  owner: string;
  denom: string;
  decimal: number;
  totalRewardPerDay: string;
  subConfigs: LiquidityRewardSubConfig[];
}

export interface LiquidityRewardDenom {
  denom: string;
  configId: Long;
}

export interface LiquidityPoolReward {
  address: string;
  configIds: Long[];
}

function createBaseLiquidityRewardSubConfig(): LiquidityRewardSubConfig {
  return { address: "", weight: 0, index: 0 };
}

export const LiquidityRewardSubConfig = {
  encode(
    message: LiquidityRewardSubConfig,
    writer: _m0.Writer = _m0.Writer.create()
  ): _m0.Writer {
    if (message.address !== "") {
      writer.uint32(10).string(message.address);
    }
    if (message.weight !== 0) {
      writer.uint32(16).int32(message.weight);
    }
    if (message.index !== 0) {
      writer.uint32(24).uint32(message.index);
    }
    return writer;
  },

  decode(
    input: _m0.Reader | Uint8Array,
    length?: number
  ): LiquidityRewardSubConfig {
    const reader = input instanceof _m0.Reader ? input : new _m0.Reader(input);
    let end = length === undefined ? reader.len : reader.pos + length;
    const message = createBaseLiquidityRewardSubConfig();
    while (reader.pos < end) {
      const tag = reader.uint32();
      switch (tag >>> 3) {
        case 1:
          message.address = reader.string();
          break;
        case 2:
          message.weight = reader.int32();
          break;
        case 3:
          message.index = reader.uint32();
          break;
        default:
          reader.skipType(tag & 7);
          break;
      }
    }
    return message;
  },

  fromJSON(object: any): LiquidityRewardSubConfig {
    return {
      address: isSet(object.address) ? String(object.address) : "",
      weight: isSet(object.weight) ? Number(object.weight) : 0,
      index: isSet(object.index) ? Number(object.index) : 0,
    };
  },

  toJSON(message: LiquidityRewardSubConfig): unknown {
    const obj: any = {};
    message.address !== undefined && (obj.address = message.address);
    message.weight !== undefined && (obj.weight = Math.round(message.weight));
    message.index !== undefined && (obj.index = Math.round(message.index));
    return obj;
  },

  fromPartial<I extends Exact<DeepPartial<LiquidityRewardSubConfig>, I>>(
    object: I
  ): LiquidityRewardSubConfig {
    const message = createBaseLiquidityRewardSubConfig();
    message.address = object.address ?? "";
    message.weight = object.weight ?? 0;
    message.index = object.index ?? 0;
    return message;
  },
};

function createBaseLiquidityRewardConfig(): LiquidityRewardConfig {
  return {
    id: Long.UZERO,
    enabled: false,
    mintable: false,
    owner: "",
    denom: "",
    decimal: 0,
    totalRewardPerDay: "",
    subConfigs: [],
  };
}

export const LiquidityRewardConfig = {
  encode(
    message: LiquidityRewardConfig,
    writer: _m0.Writer = _m0.Writer.create()
  ): _m0.Writer {
    if (!message.id.isZero()) {
      writer.uint32(8).uint64(message.id);
    }
    if (message.enabled === true) {
      writer.uint32(16).bool(message.enabled);
    }
    if (message.mintable === true) {
      writer.uint32(24).bool(message.mintable);
    }
    if (message.owner !== "") {
      writer.uint32(34).string(message.owner);
    }
    if (message.denom !== "") {
      writer.uint32(42).string(message.denom);
    }
    if (message.decimal !== 0) {
      writer.uint32(48).int32(message.decimal);
    }
    if (message.totalRewardPerDay !== "") {
      writer.uint32(58).string(message.totalRewardPerDay);
    }
    for (const v of message.subConfigs) {
      LiquidityRewardSubConfig.encode(v!, writer.uint32(66).fork()).ldelim();
    }
    return writer;
  },

  decode(
    input: _m0.Reader | Uint8Array,
    length?: number
  ): LiquidityRewardConfig {
    const reader = input instanceof _m0.Reader ? input : new _m0.Reader(input);
    let end = length === undefined ? reader.len : reader.pos + length;
    const message = createBaseLiquidityRewardConfig();
    while (reader.pos < end) {
      const tag = reader.uint32();
      switch (tag >>> 3) {
        case 1:
          message.id = reader.uint64() as Long;
          break;
        case 2:
          message.enabled = reader.bool();
          break;
        case 3:
          message.mintable = reader.bool();
          break;
        case 4:
          message.owner = reader.string();
          break;
        case 5:
          message.denom = reader.string();
          break;
        case 6:
          message.decimal = reader.int32();
          break;
        case 7:
          message.totalRewardPerDay = reader.string();
          break;
        case 8:
          message.subConfigs.push(
            LiquidityRewardSubConfig.decode(reader, reader.uint32())
          );
          break;
        default:
          reader.skipType(tag & 7);
          break;
      }
    }
    return message;
  },

  fromJSON(object: any): LiquidityRewardConfig {
    return {
      id: isSet(object.id) ? Long.fromValue(object.id) : Long.UZERO,
      enabled: isSet(object.enabled) ? Boolean(object.enabled) : false,
      mintable: isSet(object.mintable) ? Boolean(object.mintable) : false,
      owner: isSet(object.owner) ? String(object.owner) : "",
      denom: isSet(object.denom) ? String(object.denom) : "",
      decimal: isSet(object.decimal) ? Number(object.decimal) : 0,
      totalRewardPerDay: isSet(object.totalRewardPerDay)
        ? String(object.totalRewardPerDay)
        : "",
      subConfigs: Array.isArray(object?.subConfigs)
        ? object.subConfigs.map((e: any) =>
            LiquidityRewardSubConfig.fromJSON(e)
          )
        : [],
    };
  },

  toJSON(message: LiquidityRewardConfig): unknown {
    const obj: any = {};
    message.id !== undefined &&
      (obj.id = (message.id || Long.UZERO).toString());
    message.enabled !== undefined && (obj.enabled = message.enabled);
    message.mintable !== undefined && (obj.mintable = message.mintable);
    message.owner !== undefined && (obj.owner = message.owner);
    message.denom !== undefined && (obj.denom = message.denom);
    message.decimal !== undefined &&
      (obj.decimal = Math.round(message.decimal));
    message.totalRewardPerDay !== undefined &&
      (obj.totalRewardPerDay = message.totalRewardPerDay);
    if (message.subConfigs) {
      obj.subConfigs = message.subConfigs.map((e) =>
        e ? LiquidityRewardSubConfig.toJSON(e) : undefined
      );
    } else {
      obj.subConfigs = [];
    }
    return obj;
  },

  fromPartial<I extends Exact<DeepPartial<LiquidityRewardConfig>, I>>(
    object: I
  ): LiquidityRewardConfig {
    const message = createBaseLiquidityRewardConfig();
    message.id =
      object.id !== undefined && object.id !== null
        ? Long.fromValue(object.id)
        : Long.UZERO;
    message.enabled = object.enabled ?? false;
    message.mintable = object.mintable ?? false;
    message.owner = object.owner ?? "";
    message.denom = object.denom ?? "";
    message.decimal = object.decimal ?? 0;
    message.totalRewardPerDay = object.totalRewardPerDay ?? "";
    message.subConfigs =
      object.subConfigs?.map((e) => LiquidityRewardSubConfig.fromPartial(e)) ||
      [];
    return message;
  },
};

function createBaseLiquidityRewardDenom(): LiquidityRewardDenom {
  return { denom: "", configId: Long.UZERO };
}

export const LiquidityRewardDenom = {
  encode(
    message: LiquidityRewardDenom,
    writer: _m0.Writer = _m0.Writer.create()
  ): _m0.Writer {
    if (message.denom !== "") {
      writer.uint32(10).string(message.denom);
    }
    if (!message.configId.isZero()) {
      writer.uint32(16).uint64(message.configId);
    }
    return writer;
  },

  decode(
    input: _m0.Reader | Uint8Array,
    length?: number
  ): LiquidityRewardDenom {
    const reader = input instanceof _m0.Reader ? input : new _m0.Reader(input);
    let end = length === undefined ? reader.len : reader.pos + length;
    const message = createBaseLiquidityRewardDenom();
    while (reader.pos < end) {
      const tag = reader.uint32();
      switch (tag >>> 3) {
        case 1:
          message.denom = reader.string();
          break;
        case 2:
          message.configId = reader.uint64() as Long;
          break;
        default:
          reader.skipType(tag & 7);
          break;
      }
    }
    return message;
  },

  fromJSON(object: any): LiquidityRewardDenom {
    return {
      denom: isSet(object.denom) ? String(object.denom) : "",
      configId: isSet(object.configId)
        ? Long.fromValue(object.configId)
        : Long.UZERO,
    };
  },

  toJSON(message: LiquidityRewardDenom): unknown {
    const obj: any = {};
    message.denom !== undefined && (obj.denom = message.denom);
    message.configId !== undefined &&
      (obj.configId = (message.configId || Long.UZERO).toString());
    return obj;
  },

  fromPartial<I extends Exact<DeepPartial<LiquidityRewardDenom>, I>>(
    object: I
  ): LiquidityRewardDenom {
    const message = createBaseLiquidityRewardDenom();
    message.denom = object.denom ?? "";
    message.configId =
      object.configId !== undefined && object.configId !== null
        ? Long.fromValue(object.configId)
        : Long.UZERO;
    return message;
  },
};

function createBaseLiquidityPoolReward(): LiquidityPoolReward {
  return { address: "", configIds: [] };
}

export const LiquidityPoolReward = {
  encode(
    message: LiquidityPoolReward,
    writer: _m0.Writer = _m0.Writer.create()
  ): _m0.Writer {
    if (message.address !== "") {
      writer.uint32(10).string(message.address);
    }
    writer.uint32(18).fork();
    for (const v of message.configIds) {
      writer.uint64(v);
    }
    writer.ldelim();
    return writer;
  },

  decode(input: _m0.Reader | Uint8Array, length?: number): LiquidityPoolReward {
    const reader = input instanceof _m0.Reader ? input : new _m0.Reader(input);
    let end = length === undefined ? reader.len : reader.pos + length;
    const message = createBaseLiquidityPoolReward();
    while (reader.pos < end) {
      const tag = reader.uint32();
      switch (tag >>> 3) {
        case 1:
          message.address = reader.string();
          break;
        case 2:
          if ((tag & 7) === 2) {
            const end2 = reader.uint32() + reader.pos;
            while (reader.pos < end2) {
              message.configIds.push(reader.uint64() as Long);
            }
          } else {
            message.configIds.push(reader.uint64() as Long);
          }
          break;
        default:
          reader.skipType(tag & 7);
          break;
      }
    }
    return message;
  },

  fromJSON(object: any): LiquidityPoolReward {
    return {
      address: isSet(object.address) ? String(object.address) : "",
      configIds: Array.isArray(object?.configIds)
        ? object.configIds.map((e: any) => Long.fromValue(e))
        : [],
    };
  },

  toJSON(message: LiquidityPoolReward): unknown {
    const obj: any = {};
    message.address !== undefined && (obj.address = message.address);
    if (message.configIds) {
      obj.configIds = message.configIds.map((e) =>
        (e || Long.UZERO).toString()
      );
    } else {
      obj.configIds = [];
    }
    return obj;
  },

  fromPartial<I extends Exact<DeepPartial<LiquidityPoolReward>, I>>(
    object: I
  ): LiquidityPoolReward {
    const message = createBaseLiquidityPoolReward();
    message.address = object.address ?? "";
    message.configIds = object.configIds?.map((e) => Long.fromValue(e)) || [];
    return message;
  },
};

type Builtin =
  | Date
  | Function
  | Uint8Array
  | string
  | number
  | boolean
  | undefined;

export type DeepPartial<T> = T extends Builtin
  ? T
  : T extends Long
  ? string | number | Long
  : T extends Array<infer U>
  ? Array<DeepPartial<U>>
  : T extends ReadonlyArray<infer U>
  ? ReadonlyArray<DeepPartial<U>>
  : T extends {}
  ? { [K in keyof T]?: DeepPartial<T[K]> }
  : Partial<T>;

type KeysOfUnion<T> = T extends T ? keyof T : never;
export type Exact<P, I extends P> = P extends Builtin
  ? P
  : P & { [K in keyof P]: Exact<P[K], I[K]> } & Record<
        Exclude<keyof I, KeysOfUnion<P>>,
        never
      >;

if (_m0.util.Long !== Long) {
  _m0.util.Long = Long as any;
  _m0.configure();
}

function isSet(value: any): boolean {
  return value !== null && value !== undefined;
}
