/** Msg defines the Msg service. */
export interface Msg {
  Issue(
    request: DeepPartial<MsgIssue>,
    metadata?: grpc.Metadata
  ): Promise<MsgIssueResponse>;
  Mint(
    request: DeepPartial<MsgMint>,
    metadata?: grpc.Metadata
  ): Promise<MsgMintResponse>;
  Burn(
    request: DeepPartial<MsgBurn>,
    metadata?: grpc.Metadata
  ): Promise<MsgBurnResponse>;
  TransferOwnership(
    request: DeepPartial<MsgTransferOwnership>,
    metadata?: grpc.Metadata
  ): Promise<MsgTransferOwnershipResponse>;
  /** this line is used by starport scaffolding # proto/tx/rpc */
  ConfirmOwnership(
    request: DeepPartial<MsgConfirmOwnership>,
    metadata?: grpc.Metadata
  ): Promise<MsgConfirmOwnershipResponse>;
}

export class MsgClientImpl implements Msg {
  private readonly rpc: Rpc;

  constructor(rpc: Rpc) {
    this.rpc = rpc;
    this.Issue = this.Issue.bind(this);
    this.Mint = this.Mint.bind(this);
    this.Burn = this.Burn.bind(this);
    this.TransferOwnership = this.TransferOwnership.bind(this);
    this.ConfirmOwnership = this.ConfirmOwnership.bind(this);
  }

  Issue(
    request: DeepPartial<MsgIssue>,
    metadata?: grpc.Metadata
  ): Promise<MsgIssueResponse> {
    return this.rpc.unary(
      MsgIssueDesc,
      MsgIssue.fromPartial(request),
      metadata
    );
  }

  Mint(
    request: DeepPartial<MsgMint>,
    metadata?: grpc.Metadata
  ): Promise<MsgMintResponse> {
    return this.rpc.unary(MsgMintDesc, MsgMint.fromPartial(request), metadata);
  }

  Burn(
    request: DeepPartial<MsgBurn>,
    metadata?: grpc.Metadata
  ): Promise<MsgBurnResponse> {
    return this.rpc.unary(MsgBurnDesc, MsgBurn.fromPartial(request), metadata);
  }

  TransferOwnership(
    request: DeepPartial<MsgTransferOwnership>,
    metadata?: grpc.Metadata
  ): Promise<MsgTransferOwnershipResponse> {
    return this.rpc.unary(
      MsgTransferOwnershipDesc,
      MsgTransferOwnership.fromPartial(request),
      metadata
    );
  }

  ConfirmOwnership(
    request: DeepPartial<MsgConfirmOwnership>,
    metadata?: grpc.Metadata
  ): Promise<MsgConfirmOwnershipResponse> {
    return this.rpc.unary(
      MsgConfirmOwnershipDesc,
      MsgConfirmOwnership.fromPartial(request),
      metadata
    );
  }
}

